.Onsell-products {
  width: 92%;
  margin: 0 auto;
}

.Onsell-products h2 {
  color: red;
  font-size: 1.25rem;
  text-align: center;
  width: 80%;
  margin: 1em auto;
  padding: 0.5em 0;
  border: 1px solid red;
}

.Onsell-products .Card {
  width: 300px;
  position: relative;
  background-color: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-bottom: 1em;
}

.Onsell-products .Card header {
  width: 300px;
  height: 300px;
  padding: 1em;
}

.Onsell-products .Card header h3 {
  position: absolute;
  font-size: 0.875rem;
  display: flex;
  justify-content: space-between;
}

.Onsell-products .Card header h4 {
  display: none;
  position: absolute;
  right: 5em;
  margin-top: -6em;
}

.Onsell-products .Card header h4 span {
  font-size: 0.875rem;
  display: block;
  background-color: #fff;
  margin-bottom: 0.5em;
  padding: 0.5em 1em;
}

.Onsell-products .Card figure {
  display: flex;
  position: absolute;
  width: 38px;
  height: 38px;
  right: 2em;
  margin-top: -1.2em;
  background-color: red;
  border-radius: 50%;
  z-index: 1;
}

.Onsell-products .Card figure img {
  height: 14px;
  margin: auto;
}

.Onsell-products .Card footer {
  position: relative;
  height: 200px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1em 1em;
}

.Onsell-products .Card footer button {
  color: #fff;
  width: 100%;
  padding: 1em;
  background-color: #000;
}

.Onsell-products .Card footer p {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.875rem;
  margin: 1em 0;
  line-height: 1.2rem;
}

@media (min-width: 30em) {
  .Onsell-products {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 2em;
  }

  .Onsell-products h2 {
    width: 100%;
  }

  .Onsell-products .Card {
    margin-bottom: 3em;
  }
}

