.Product-gallery {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 84%;
  margin: 0 auto;
}

.Product-gallery > div:first-of-type {
  width: 100%;
  margin-bottom: 0.6em;
}

.Product-gallery > div:last-of-type {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Product-gallery > div:last-of-type div {
  width: 48%;
  height: 48%;
  margin-bottom: 0.7em;
}

.Product-gallery div {
  position: relative;
}

.Product-gallery div h2 {
  font-weight: 500;
  font-size: 0.875rem;
  text-align: center;
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.Product-gallery div h2 span {
  display: block;
  margin: auto;
  padding: 0.4em 0.8em;
  background-color: #fff;
  border-radius: 35px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
}

@media (min-width: 30em) {
}

@media (min-width: 50em) {
  .Product-gallery {
    flex-direction: row;
    justify-content: space-between;
  }

  .Product-gallery > div:first-of-type {
    width: 49%;
  }

  .Product-gallery > div:last-of-type {
    width: 49%;
  }
}
