.App-banner {
  width: 86%;
  margin: 3em 0;
  margin: 0 auto;
}

.App-banner .categories {
  width: 100%;
  height: auto;
  margin: auto 0;
  /* background-color: black; */
}

.App-banner .categories ul {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
}

.App-banner .categories ul li {
  position: relative;
  width: 100%;
  /* height: 10%; */
}

.App-banner .categories ul li a {
  font-size: 0.875rem;
  color: #fff;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1em;
  background-color: #000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.App-banner .categories ul li img {
  height: 18px;
  margin-right: 1em;
}

.App-banner .categories > ul > li > a:hover {
  transition: ease-in 2s;
  opacity: 0.5;
}

.App-banner .categories ul li span {
  width: 75%;
  display: inline-block;
  margin: auto 0;
}

.App-banner .categories ul li:first-of-type {
  display: inline-block;
}

.App-banner .categories > ul > li:first-of-type {
  position: relative;
  font-size: 0.875rem;
  color: #fff;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1em;
  background-color: red;
  z-index: 2;
}

.App-banner .categories > ul > li:first-of-type span {
  width: 85%;
}

.App-banner .categories ul ul {
  display: none;
  height: 100%;
  color: black;
  position: absolute;
  top: 0;
  left: 300px;
  background-color: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}

.App-banner .categories ul ul li {
  height: 49px;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.App-banner .categories ul ul a {
  display: inline-block;
  height: 1005;
  color: black;
  padding: 1.4em;
}

.App-banner .categories ul ul li a:hover {
  color: red;
  transition: ease-in 1s;
  opacity: 0.5;
}

.App-banner .slider .App-banner .categories .subcategories div li {
  display: none;
}

.App-banner .subcategories {
  display: none;
}

.App-banner .slider {
  position: relative;
  width: 100%;
  height: 500px;
  margin: 1em 0;
  background-color: seashell;
}

.App-banner .slider div {
  color: #fff;
  position: absolute;
  padding: 1em;
  bottom: 1em;
}

.App-banner .slider div h2 {
  font-size: 2.8rem;
  font-weight: 300;
  line-height: 3rem;
}

.App-banner .slider div p {
  margin: 1em 0;
  line-height: 1.5rem;
}

.App-banner .slider div button {
  color: #fff;
  padding: 1em 1.8em;
  border-radius: 35px;
  background-color: transparent;
  border: 2px solid #fff;
}

.App-banner .slider figure {
  display: flex;
  width: 100%;
  height: 100%;
}

.App-banner .slider img {
  width: auto;
  height: 100%;
}

@media (min-width: 30em) {
  .App-banner .categories ul li a {
    font-size: 1rem;
  }
}

@media (min-width: 50em) {
  .App-banner {
    display: flex;
    justify-content: space-between;
    margin: 1em auto;
  }

  .App-banner .categories {
    width: 450px;
    margin: 0;
    margin-right: 1em;
  }

  .App-banner .subcategories {
    display: inline-block;
  }

  .App-banner .slider {
    margin: 0;
    height: 505px;
  }

  .App-banner .slider img {
    width: 100%;
    height: 100%;
  }
}
